<template>
  <div class="dashboard">
    <nav class="dashboard-nav">
      <div class="brand">
        <i class="fas fa-chart-line"></i>
        <span>Stok Yönetim</span>
      </div>
      
      <div class="nav-actions">
        <button @click="logout" class="logout-btn">
          <i class="fas fa-sign-out-alt"></i>
          Çıkış Yap
        </button>
      </div>
    </nav>
    
    <!-- Main Content -->
    <div class="dashboard-content">
      <!-- Stats Cards -->
      <div class="stats-grid">
        <div class="stat-card" v-for="stat in stats" :key="stat.id">
          <div class="stat-icon" :class="stat.color">
            <i :class="stat.icon"></i>
          </div>
          <div class="stat-info">
            <h3>{{ stat.value }}</h3>
            <p>{{ stat.label }}</p>
          </div>
        </div>
      </div>

      <!-- Products Table -->
      <div class="products-section">
        <div class="section-header">
          <h2>Ürünler</h2>
          <button @click="showAddModal = true" class="add-btn">
            <i class="fas fa-plus"></i> Yeni Ürün
          </button>
        </div>

        <div class="table-container" v-if="!loading">
          <table class="products-table">
            <thead>
              <tr>
                <th>Ürün Adı</th>
                <th>Stok</th>
                <th>Fiyat</th>
                <th>Durum</th>
                <th>İşlemler</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="product in products" :key="product.id">
                <td>{{ product.name }}</td>
                <td>{{ product.stock }}</td>
                <td>{{ formatPrice(product.price) }}</td>
                <td>
                  <span :class="getStatusClass(product.stock)">
                    {{ getStatusText(product.stock) }}
                  </span>
                </td>
                <td>
                  <button @click="editProduct(product)" class="action-btn edit">
                    <i class="fas fa-edit"></i>
                  </button>
                  <button @click="deleteProduct(product.id)" class="action-btn delete">
                    <i class="fas fa-trash"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <!-- Loading Skeleton -->
        <div v-else class="skeleton-loader">
          <div class="skeleton-row" v-for="n in 5" :key="n"></div>
        </div>
      </div>
    </div>

    <!-- Add/Edit Modal -->
    <modal v-if="showAddModal" @close="showAddModal = false">
      <template #header>
        <h3>{{ editingProduct ? 'Ürün Düzenle' : 'Yeni Ürün Ekle' }}</h3>
      </template>

      <template #body>
        <form @submit.prevent="handleSubmit" class="product-form">
          <div class="form-group">
            <label>Ürün Adı</label>
            <input v-model="form.name" type="text" required>
          </div>
          <div class="form-group">
            <label>Stok Miktarı</label>
            <input v-model.number="form.stock" type="number" required>
          </div>
          <div class="form-group">
            <label>Fiyat</label>
            <input v-model.number="form.price" type="number" step="0.01" required>
          </div>
        </form>
      </template>

      <template #footer>
        <button @click="showAddModal = false" class="btn-secondary">İptal</button>
        <button @click="handleSubmit" class="btn-primary">Kaydet</button>
      </template>
    </modal>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { stockService } from '@/services/api';
import Modal from '@/components/Modal.vue';
import { useRouter } from 'vue-router';

export default {
  name: 'Dashboard',
  components: { Modal },
  setup() {
    const loading = ref(false);
    const products = ref([]);
    const showAddModal = ref(false);
    const editingProduct = ref(null);
    const form = ref({
      name: '',
      stock: 0,
      price: 0
    });

    const stats = ref([
      {
        id: 1,
        icon: 'fas fa-box',
        value: '0',
        label: 'Toplam Ürün',
        color: 'blue'
      },
      {
        id: 2,
        icon: 'fas fa-exclamation-triangle',
        value: '0',
        label: 'Kritik Stok',
        color: 'red'
      },
      {
        id: 3,
        icon: 'fas fa-chart-line',
        value: '₺0',
        label: 'Toplam Değer',
        color: 'green'
      }
    ]);

    // Methods...
    const loadProducts = async () => {
      loading.value = true;
      try {
        const response = await stockService.getProducts();
        products.value = response.data;
        updateStats();
      } catch (error) {
        console.error('Error loading products:', error);
      } finally {
        loading.value = false;
      }
    };

    const updateStats = () => {
      const totalProducts = products.value.length;
      const criticalStock = products.value.filter(p => p.stock < 10).length;
      const totalValue = products.value.reduce((sum, p) => sum + (p.price * p.stock), 0);

      stats.value[0].value = totalProducts;
      stats.value[1].value = criticalStock;
      stats.value[2].value = formatPrice(totalValue);
    };

    const formatPrice = (price) => {
      return new Intl.NumberFormat('tr-TR', {
        style: 'currency',
        currency: 'TRY'
      }).format(price);
    };

    const getStatusClass = (stock) => {
      if (stock <= 0) return 'status-out';
      if (stock < 10) return 'status-low';
      return 'status-ok';
    };

    const getStatusText = (stock) => {
      if (stock <= 0) return 'Tükendi';
      if (stock < 10) return 'Kritik';
      return 'Yeterli';
    };

    const handleSubmit = async () => {
      try {
        if (editingProduct.value) {
          await stockService.updateProduct(editingProduct.value.id, form.value);
        } else {
          await stockService.addProduct(form.value);
        }
        await loadProducts();
        showAddModal.value = false;
      } catch (error) {
        console.error('Error saving product:', error);
      }
    };

    const editProduct = (product) => {
      editingProduct.value = product;
      form.value = { ...product };
      showAddModal.value = true;
    };

    const deleteProduct = async (id) => {
      if (confirm('Bu ürünü silmek istediğinize emin misiniz?')) {
        try {
          await stockService.deleteProduct(id);
          await loadProducts();
        } catch (error) {
          console.error('Error deleting product:', error);
        }
      }
    };

    // Lifecycle hooks
    const router = useRouter();
    onMounted(async () => {
      const token = localStorage.getItem('token');
      if (!token) {
        router.push('/login');
        return;
      }

      try {
        // Token'ı her API isteğine ekle
        const response = await stockService.getProducts();
        products.value = response.data;
        updateStats();
      } catch (error) {
        console.error('Error loading products:', error);
        if (error.response?.status === 401) {
          // Token geçersiz veya süresi dolmuş
          localStorage.removeItem('token');
          router.push('/login');
        }
      }
    });

    return {
      loading,
      products,
      showAddModal,
      editingProduct,
      form,
      stats,
      // Methods
      loadProducts,
      updateStats,
      formatPrice,
      getStatusClass,
      getStatusText,
      handleSubmit,
      editProduct,
      deleteProduct
    };
  }
};
</script>

