<template>
  <div class="signup-container">
    <div class="signup-card">
      <div class="signup-header">
        <h2>14 Gün Ücretsiz Deneyin</h2>
        <p>Hemen kayıt olun, stok yönetimini kolaylaştırın!</p>
      </div>
      
      <form @submit.prevent="handleSignup" class="signup-form">
        <div class="form-group">
          <label>
            <i class="fas fa-building"></i>
            Şirket Adı
          </label>
          <input 
            v-model="form.company" 
            type="text" 
            required
            placeholder="Şirketinizin adı"
          >
        </div>

        <div class="form-group">
          <label>
            <i class="fas fa-envelope"></i>
            Email
          </label>
          <input 
            v-model="form.email" 
            type="email" 
            required
            placeholder="ornek@sirket.com"
          >
        </div>

        <div class="form-group">
          <label>
            <i class="fas fa-lock"></i>
            Şifre
          </label>
          <div class="password-input">
            <input 
              v-model="form.password"
              :type="showPassword ? 'text' : 'password'"
              required
              placeholder="Güçlü bir şifre oluşturun"
            >
            <button 
              type="button"
              @click="showPassword = !showPassword"
              class="toggle-password"
            >
              <i :class="showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'"></i>
            </button>
          </div>
        </div>

        <button type="submit" class="signup-btn">
          <span>Ücretsiz Başla</span>
          <i class="fas fa-arrow-right"></i>
        </button>
      </form>

      <div class="signup-footer">
        <p>Zaten hesabınız var mı? <router-link to="/login">Giriş Yapın</router-link></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Signup',
  data() {
    return {
      form: {
        company: '',
        email: '',
        password: ''
      },
      showPassword: false,
      loading: false
    }
  },
  methods: {
    async handleSignup() {
      this.loading = true;
      try {
        const response = await fetch('/api/auth/signup', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            email: this.form.email,
            password: this.form.password,
            company: this.form.company
          })
        });

        const data = await response.json();

        if (data.status === 'success') {
          // Token'ı sakla
          localStorage.setItem('token', data.data.token);
          localStorage.setItem('isLoggedIn', 'true');
          localStorage.setItem('userEmail', this.form.email);

          // Başarılı kayıt mesajı
          alert('Kayıt başarılı! Dashboard\'a yönlendiriliyorsunuz.');
          
          // Dashboard'a yönlendir
          this.$router.push('/dashboard');
        } else {
          throw new Error(data.message);
        }
      } catch (error) {
        console.error('Signup error:', error);
        alert(error.message || 'Kayıt işlemi başarısız');
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>

